.controls {
  text-align: center;
}

.add-button {
  padding: 12px 24px;
  font-size: 1.1rem;
  font-weight: bold;
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  height: 48px; /* Ensure consistent height */
  line-height: 1.2; /* Center text vertically */
}

.add-button:hover:not(:disabled) {
  background-color: #218838;
}

.add-button:disabled {
  background-color: #6c757d;
  cursor: not-allowed;
}

.add-button.animating {
  background-color: #17a2b8;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
} 