.board-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
}

.board-container .loading {
  font-size: 1.2rem;
  color: #666;
  padding: 20px;
}

.game-board {
  border-collapse: collapse;
  background-color: #f8f9fa;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.game-board td {
  width: 60px;
  height: 60px;
  border: 2px solid #dee2e6;
  text-align: center;
  vertical-align: middle;
  font-size: 24px;
  font-weight: bold;
  background-color: white;
  cursor: pointer;
  transition: all 0.2s ease;
  user-select: none;
}

.game-board td:hover {
  background-color: #e9ecef;
  transform: scale(1.05);
}

.game-board td:empty {
  background-color: #f8f9fa;
  cursor: default;
}

.game-board td:empty:hover {
  background-color: #f8f9fa;
  transform: none;
}

.game-board td.selected {
  background-color: #007bff;
  color: white;
  transform: scale(1.1);
  box-shadow: 0 2px 8px rgba(0, 123, 255, 0.3);
}

.game-board td.selected:hover {
  background-color: #0056b3;
}

/* Animation for newly placed numbers */
@keyframes dropIn {
  0% {
    transform: translateY(-20px) scale(0.8);
    opacity: 0;
  }
  50% {
    transform: translateY(5px) scale(1.1);
    opacity: 0.8;
  }
  100% {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    background-color: #e3f2fd;
  }
  100% {
    opacity: 1;
    background-color: white;
  }
}

.game-board td.new-number {
  animation: dropIn 0.3s ease-out;
  box-shadow: 0 0 10px rgba(0, 123, 255, 0.3);
}

.game-board td.empty-cell {
  animation: fadeIn 0.5s ease-in;
}

/* Additional animation for the board expansion */
@keyframes expandBoard {
  0% {
    transform: scale(0.95);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.board-container.expanding {
  animation: expandBoard 0.3s ease-out;
}

/* Shake animation for invalid moves */
@keyframes shake {
  0%, 100% { transform: translateX(0); }
  10%, 30%, 50%, 70%, 90% { transform: translateX(-5px); }
  20%, 40%, 60%, 80% { transform: translateX(5px); }
}

.board-container.shake {
  animation: shake 0.5s ease-in-out;
} 